type TaskData = {
  id: string;
  title: string;
  state: "TASK_ARCHIVED" | "TASK_INBOX" | "TASK_PINNED";
};

type TaskProps = {
  task: TaskData;
  onArchiveTask: (id: string) => void;
  onPinTask: (id: string) => void;
};

export default function Task({
  task: { id, title, state },
  onArchiveTask,
  onPinTask,
}: TaskProps) {
  return (
    <main
      className="flex items-center justify-between p-2 border-b border-slate-200"
      onClick={() => {
        onArchiveTask(id);
        onPinTask(id);
      }}
    >
      heloo
    </main>
  );
}
